.container {
    width: 100%;
    min-width: 455px;
    font-size: 12px;
    .header {
        height: 48px;
        background: #f6f8fa;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        .btn {
            position: absolute;
            right: 16px;
            color: #0f0f0f;
        }
        .text {
            font-size: 14px;
            color: #0f0f0f;
            font-weight: 600;
            display: flex;
            align-items: center;
        }
        .yearText {
            margin: 0 14px;
        }
        .dropIcon {
            cursor: pointer;
            &:hover {
                fill: #00b831;
            }
        }
    }

    .dayRow {
        display: flex;
        border-bottom: 1px solid #ebedf0;
        margin-top: 11px;
        // height: 32px;
        padding-bottom: 3px;

        .item {
            flex: 1;
            width: 12px;

            font-size: 12px;
            color: #121212;

            font-weight: 400;
            padding: 0 5px;
        }
    }
    .row {
        display: flex;
        border-bottom: 1px solid #ebedf0;
        .item {
            flex: 1;
            height: 78px;
            padding: 5px;
            cursor: pointer;
            transition: all 0.3s ease;
            &:hover {
                background: #f3f3f6;
            }
        }
        .active {
            background: #ebebf0;
            &:hover {
                background: #ebebf0;
            }
        }
    }
    .disable {
        color: #93959e;
        background-color: #f0f0f0;
        &:hover {
            background: #f0f0f0;
        }
        &.active {
            background-color: #f0f0f0;
            &:hover {
                background-color: #f0f0f0;
            }
        }
    }
    .noCurrentMonth {
        color: #e0e3e7;
    }
}
